.container {
    max-width: 1140px;
    margin: auto;
    color: gray;
}

.heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    box-shadow: 0px 0px 6px rgb(185, 43, 185);
    border-radius: 8px;
    margin: 2rem 1rem;
    padding: .7rem 1rem;
    font-weight: 700;
}

.coin-name {
    margin-left: -4rem;
}

.coin-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    box-shadow: 0px 0px 6px rgb(185, 43, 185);
    border-radius: 8px;
    margin: 2rem 1rem;
    padding: .7rem 1rem;
    color: gray;
    transition: .5s ease-in-out;
}

.coin-row:hover {
    transform: scale(1.05);
    
    cursor: pointer;
}

img {
    height: 40px;
    margin-right: 8px;
    width: auto;
}

.img-symbol {
    display: flex;
    align-items: center;
}

@media screen and (max-width: 720px) {
    .hide-mobile {
        display: none;
    }
}